<template>
  <div class="MessageTo"
       @contextmenu.prevent="$refs.menu.open"
       @mouseover="showDate = true"
       @mouseleave="showDate = false"
       ref="msgBOX"

  >
   <p class="username">{{author}}</p>
    <span class="text" >{{msg}}</span>
    <span v-if="showDate" class="date">{{date}}</span>
    <vue-context ref="menu">
      <li>
        <a href="#" @click.prevent="onClick($event.target.innerText)">Edit</a>
      </li>
      <li>
        <a href="#" @click.prevent="onClick($event.target.innerText)">Delete</a>
      </li>
    </vue-context>
  </div>
</template>

<script>
import axios from "axios";
import M from 'materialize-css'
var $ = require('jquery')
import VueContext from 'vue-context';
export default {
  name: "MessageTo",
  props: [
      'msg',
      'author',
      'date',
      'msgId'
  ],
  data(){
    return{
      showDate: false
    }
  },
  components: {
    VueContext
  },
  methods: {
    editMsg(){
      let text = prompt("Enter new text: ");
      axios.put(this.$store.state.request_url + "message/" + this.msgId, {
        "text": text.toString()
      });
    },
    onClick (text) {
      if(text === "Edit"){
          this.editMsg()
      }
      if(text == "Delete")
        this.deleteMsg()
    },
    deleteMsg(){
      axios.delete(this.$store.state.request_url + "message/" + this.msgId)
    },
    fixMargin(){
      let width = this.$refs.msgBOX.clientWidth;
      //console.log(width)

      this.$refs.msgBOX.style.marginLeft = "calc(100% - "+width+"px - 50px)"

    }
  },
  computed:{
  },
  mounted() {
    $( ".MessageTo" ).on( "mouseleave", function() {
        $("body").trigger("click")
    });
    M.AutoInit(undefined);
    this.fixMargin()
  }
}
</script>

<style scoped>
@import '~vue-context/dist/css/vue-context.css';
.MessageTo{
  right: 10px;
  margin: 10px 10px 10px 70%;
  background-color: lightgray;
  float: right;
}

.MessageTo .date{
  left: -200px;
}
.MessageTo .props{
  position: absolute;
  right: 0px;
  bottom: 0px;
}
.MessageTo .props a{
  margin-right: 10px;
}
</style>

