<template>
  <div class="MessageFrom"
       @mouseover="showDate = true"
       @mouseleave="showDate = false"
  >
    <p class="username">{{username}}</p>
    <span class="text">{{msg}}</span>
    <span v-if="showDate" class="date">{{date}}</span>
  </div>
</template>

<script>
export default {
  name: "MessageFrom",
  props: [
      'msg',
      'username',
      'date'
  ],
  data(){
    return{
      showDate: false
    }
  }
}
</script>

<style scoped>
.MessageFrom .date{

}


</style>