<template>
  <div class="msgForm">
    <div ref="messageBox" class="messages">
      <div v-for="msg in messages" v-bind:key="msg.messageId">
        <div  v-if="checkUser(msg.username)" ><MessageTo class="msgBOX"   :msg="msg.text"  :author="msg.username" :msgId="msg.id" :date="msg.createdDate"></MessageTo></div>
        <div v-else> <MessageFrom  class="msgBOX"  :msg="msg.text" :username="msg.username"  :date="msg.createdDate"></MessageFrom></div>
      </div>

    </div>
    <MessageSend></MessageSend>
    </div>

</template>

<script>
import MessageFrom from "@/components/MessageFrom";
import MessageTo from "@/components/MessageTo";
import MessageSend from "@/components/MessageSend";
import axios from "axios";
var $ = require('jquery')
export default {
name: "MessengerForm",
  components: {MessageSend, MessageTo, MessageFrom},
  data(){
    return{
      messages: [],
      msgId: 0,
      myMsg: '',
      value: '',
      state: true
    }
  },
  props: {
    user: null
  },
  comments: [
    MessageFrom,
    MessageTo
  ],
  methods: {

    checkState(){
      let vm = this
      axios.get(this.$store.state.request_url + "message/check").then(function (response){
        vm.state = response.data
      });
    },
    initial(){
      axios.get(this.$store.state.request_url + "message/initial")

    },
   getMsg(){
      if(this.state) {
        let vm = this;
        vm.messages = [];
        axios.get(this.$store.state.request_url + "message")
            .then(function (response) {
              let data = response.data;
              if (data) {
                data.forEach(function (e) {
                  let msgContent = {
                    text: e.text,
                    userId: e.user_id,
                    createdDate: e.create_date,
                    id: e.message_id,
                    username: e.username
                  }
                  var s = msgContent.createdDate;
                  var n = s.indexOf('.');
                  s = s.substring(0, n != -1 ? n : s.length).replace("T", " ");

                  msgContent.createdDate = s
                  vm.messages.push(msgContent)
                })
              }
            }).catch(function (error) {
          console.log(error)
        });

      }


   },
    checkUser(name){
      return (name === this.user)
    }
  },

  created() {
    let vm = this;
    this.checkUser("dito");
    vm.initial()

     setInterval(function (){
        vm.checkState()
       // console.log(vm.messages)
       vm.getMsg()
     }
     , 500)
    $(".messages").animate({ scrollTop: $(document).height()*10000 }, 100);

  }
}

</script>

<style >

.msgForm{
  position: absolute;
  height: 100%;
  left: 300px;
  width: calc(90% - 200px);
}


.messages{
  height: calc(100% - 75px);
  position: relative;

  overflow-y: auto;
}
.msgBOX{
  display:inline-block;
  justify-content: center;
  text-align: center;
  word-break: break-all;
  min-height: 10px;
  max-width: 40%;
  border-radius: 10px;
  margin-top: 20px;
  padding: 5px 15px;
  background-color: lightblue;
  position: relative;
}
.msgBOX .date{
  background-color: lightgreen;
  width: 200px;
  margin-top: -20px;
  position: absolute;
  z-index: 20;
  color: black;
  border: 1px solid black;
  border-radius: 3px;
}
.msgBOX .text{
  font-size: 1vw;
  display: inline;
}
.msgBOX .username{
  color: rgba(0, 0,0,0.4);
  white-space: nowrap;
  position: absolute;
  display: inline-block;
  word-break: normal;
  left: 2px;
  top: -35px;

}
</style>