<template>
  <div>
    <div v-if="pageLoaded">
      <Chat v-if="islogged"/>
      <Login v-else/>
    </div>
    <div v-else>

    </div>
  </div>
</template>

<script>
import axios from "axios";
import Login from "@/views/Login";
import Chat from "@/views/Chat";
var $ = require('jquery')
export default {
  name: "Home",
  components: {
    Chat,
    Login
  },
  data() {
    return{
      islogged: false,
      pageLoaded: false
    }
  },
  methods: {
    checkLogged(){
      let vm = this
      axios.get(this.$store.state.request_url + 'user/is-logged')
          .then(function (response) {
            vm.islogged = response.data
          })
          .catch(function (error) {
            console.log(error);
          })
          .then(function () {
          //  vm.islogged = false
          });

    }
  },
  mounted() {
    let vm = this
    vm.checkLogged()
    $(document).ready(function (){
      vm.pageLoaded = true
    })
  }
}
</script>

<style scoped>
.progress{
  position: absolute;
  top: 50%;
}
</style>