<template>
  <div class="MessageSend">
    <div class="row">
      <form class="col s12" id="msgSEND" v-on:submit.prevent="noop">
        <div class="row">
          <div class="input-field col s6">
            <i class="material-icons prefix textInputTrigger" >mode_edit</i>
            <input type="text" id="icon_prefix2" v-model="value" class="materialize-textarea" @keyup="sendMsgOnKey" />
            <label for="icon_prefix2" class="textInputTrigger">Text</label>
          </div>
          <button id="emojiSelector" @mouseenter="showEmoji"></button>

          <VEmojiPicker @select="selectEmoji" />
        </div>

      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { VEmojiPicker } from 'v-emoji-picker';
import M from 'materialize-css'
import VueContext from 'vue-context';
var $ = require('jquery')
export default {
  name: "MessageSend",
  data(){
    return{
      value: '',
      input: '',
      search: ''
    }
  },
  components:{
    VEmojiPicker
  },
  methods: {
    getValue: function (){
      console.log(this.value + " " + this.user);
    },
    selectEmoji(emoji) {
      console.log(emoji)
      this.value += emoji.data
      $('.textInputTrigger').addClass('active')
    },
    sendMsg: function () {
      const vm = this;
      if (vm.value !== '') {
        axios({
          method: 'post',
          url: this.$store.state.request_url + "message",
          data: {
            "text": vm.value
          }
        }).then(function (response){

        });
        vm.value = ""
      }
    },
    showEmoji(){
      $("#EmojiPicker").css("visibility", "visible")

    },
    sendMsgOnKey: function(e) {
      if (e.keyCode === 13) {
        //Enter
       // alert('Enter was pressed');
        this.sendMsg()
      }
      this.log += e.key;
    },
    noop () {
      // do nothing ?
    }
  },
  mounted() {
    M.AutoInit(undefined);
    $( "#EmojiPicker" ).on( "mouseleave", function() {
      $("#EmojiPicker").css("visibility", "hidden")
    });
  }
}
</script>

<style >
@import '~vue-context/dist/css/vue-context.css';
.MessageSend{
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 75px !important;


}

.MessageSend *{
margin-bottom: 0 !important;
}
#icon_prefix2{
  width: 190% !important;

}
#EmojiPicker{
  position: absolute;
  width: 320px;
  right: 0%;
  z-index: 10000;
  top: -400px;
  visibility: hidden;
  max-height: 450px;
  transform: scale(0.9);
}
#emojiSelector{
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;
  outline:none;
  position: absolute;
  right: 0px;
  font-size: 1.5vw;
}

.MessageSend button{
  top: 20px;
}



</style>