<template>
  <div>

    <button  id="btn-userinfo" v-on:click="userInfoShow = true" class="btn waves-effect waves-light" type="submit" name="action">User Info
    </button>
     <button  id="btn-logout"  v-on:click="logout()" class="btn waves-effect waves-light" type="submit" name="action">Logout
      <i class="material-icons right">send</i>
      </button>
      <MessengerForm :user="userInfo.username"></MessengerForm>
    <!--USER INFO-->
    <div id="user-infoBack" v-if="userInfoShow">
      <div id="user-info">
        <a id="close-user-info"  v-on:click="userInfoShow = false; editUserBack()" class="btn-floating btn-large waves-effect waves-light light-green-text"><i class="material-icons">clear</i></a>
        <div v-if="userInfoShow1">
          <ul>
            <li>Username: {{userInfo.username}}</li>
            <li>firstname: {{userInfo.firstname}}</li>
            <li>lastname: {{userInfo.lastname}}</li>
            <li>Create Date: {{userInfo.createDate}}</li>
          </ul>
          <div id="ops">
          <button v-on:click="deleteUser()" class="btn waves-effect waves-light" type="submit" name="action">Delete Account
          </button>
          <button v-on:click="editUser()" class="btn waves-effect waves-light" type="submit" name="action">Edit
          </button>
        </div>
     </div>
        <!--EDIT-->
        <div v-else>
          <div class="row">
            <form class="col s12">
              <div class="row">
                <div class="input-field col s12">
                  <input id="reg_firstname" type="text" class="validate" v-bind:value="userInfo.firstname" v-model="editUserInfo.firstname">
                  <label for="reg_firstname" class="active">Firstname</label>
                </div>
              </div>
              <div class="row">
                <div class="input-field col s12">
                  <input id="reg_lastname" type="text" class="validate" v-bind:value="userInfo.lastname" v-model="editUserInfo.lastname">
                  <label for="reg_lastname" class="active">Lastname</label>
                </div>
              </div>
              <div class="row">
                <div class="input-field col s12">
                  <input id="reg_password" type="password" class="validate" v-model="editUserInfo.password" >
                  <label for="reg_password">Password</label>
                </div>
              </div>
              <button v-on:click="editUserBack()" class="btn waves-effect waves-light" type="submit" name="action">Back
              </button>
              <button v-on:click="editUserSubmit()" class="btn waves-effect waves-light" type="submit" name="action">Submit
              </button>
            </form>

          </div>
        </div>
  </div>
  </div>
  </div>
</template>

<script>
import axios from "axios";
import MessengerForm from "@/components/MessengerForm";
var $ = require('jquery')
export default {
  name: "Chat",
  components: {MessengerForm},
  data(){
    return{
      userInfo: {
        userId: "",
        username: "",
        firstname: "1111",
        lastname: "",
        createDate: ""
      },
      editUserInfo: {
        firstname: "",
        lastname: "",
        password: ""
      },
      userInfoShow: false,
      userInfoShow1: true
    }
  },
  methods: {
    getUserInfo(){
      let vm = this
      axios.get(this.$store.state.request_url + "user").
      then(function (response){
        vm.userInfo.userId = response.data.userId
        vm.userInfo.username = response.data.username
        vm.userInfo.firstname = response.data.firstname
        vm.userInfo.lastname = response.data.lastname
      var s = response.data.createDate
/*
        vm.userInfo.username = "ditok"
        vm.userInfo.firstname = "Dimitri"
        vm.userInfo.lastname =  "Kvirikashvili"
        var s = "2021-04-14 19:22:59.076221+04"*/
        var n = s.indexOf(" ");
        s = s.substring(0, n != -1 ? n : s.length);
        vm.userInfo.createDate = s
      })
    },
    logout(){
      axios.post(this.$store.state.request_url + "user/logout")
    },
    deleteUser(){
      let answer = prompt("Are you sure? y/n")
      if(answer === "y")
       axios.delete(this.$store.state.request_url + "user")
    },
    editUser(){
      this.userInfoShow1 = false
      $("#user-info").height(350).css("top", "calc(40% - " + 125 + "px)")
    },
    editUserBack(){
      this.userInfoShow1 = true
      $("#user-info").height(200).css("top", "calc(40% - " + 100 + "px)")
    },
    editUserSubmit(){
      let vm = this
        axios.put(this.$store.state.request_url + "user", {
            "firstname": vm.editUserInfo.firstname,
            "lastname": vm.editUserInfo.lastname,
            "password": vm.editUserInfo.password
        });
    }
  },
  created() {
    this.getUserInfo()
  }
}
</script>

<style scoped>
#user-infoBack{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  z-index: 100;
}
#close-user-info{
  position: absolute;
  right: -25px;
  top: -25px;
  transform: scale(0.5);
}
#user-info{
  position: absolute;
  margin: auto;
  font-size: 20px;
  width: 300px;
  height: 200px;
  left: calc(50% - 150px);
  top: calc(40% - 100px);
  background-color: white;
  border: 2px solid black;
  z-index: 100;
}
#user-info ul{

  position: absolute;

  width: 100%;
  height: 80%;
  left: 20px;
}
#user-info button{

  bottom: 0;
  font-size: 1vw;
  width: 45%;
  margin-inline: 2.5%;
}
#btn-userinfo{
  position: absolute;
  top: 10px;
  left: 50px;
}
#btn-logout{
  position: absolute;
  top: 60px;
  left: 50px;
}
#ops{
  position: absolute;
  bottom: 0;
  width: 100%;
}
div > .row{

}
</style>